@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap');
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


.rock {
  background-color: rgb(148, 130, 81);
}
.ghost {
  background-color: rgb(247, 247, 247);
}
.electric {
  background-color: rgb(255, 255, 161);
}
.bug {
  background-color: #F6D6A7;
}
.poison {
  background-color: #d9c6e0;
}
.normal {
  background-color: #e9e3e3;
}
.fairy {
  background-color: rgba(255, 192, 203, 0.863);
}
.fire {
  background-color: #f3cfc9;
}
.grass {
  background-color:#d5f5d4;
}
.water {
  background-color:#E0F1FD;
}
.steel{
  background-image: linear-gradient(rgb(179, 179, 179), rgb(255, 255, 255), rgb(183, 189, 183));
}
.psychic{
  background-color:#adddd1;
}
.ground{
  background-color: #948881;
}
.dark{
  background-color: #3a3a3a;
  color:white;
}

.app-contaner {
  font-family: 'Montserrat', sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  padding: 3rem 2rem;
}
.pokemon-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.all-container {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}
.thumb-container {
  flex-basis:15%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 2rem 1rem ;
  margin: 1rem;
  border: 1px solid #efefef;
  border-radius: 1rem;
  max-width: 200px;
  text-align: center;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.29);
}

@media only screen and (max-width: 1000px) {
  .thumb-container {
  flex-basis:30%;
  }
  .app-contaner{
    padding: 3rem 1rem;
  }
}

@media only screen and (max-width: 720px) {
  .thumb-container {
  flex-basis:30%;
  }
  .app-contaner{
    padding: 3rem 1rem;
  }
}
@media only screen and (max-width: 535px) {
  .thumb-container {
  flex-basis:40%;
  }
  .load-more {
    font-size: 0.5rem;
  }
}

h3 {
  margin-bottom: 0.2rem;
}
.thumb-container .number {
  border-radius: 1rem;
  padding: 0.2rem 0.4rem;
  background-color: rgba(255,255,255,0.3);
}
.thumb-container img {
  width: 120px;
  height: 120px;
}
.thumb-container small {
  text-transform: capitalize;
}
.detail-wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
}
.detail-wrapper button {
  color: rgb(22, 22, 22);
  padding: .5rem;
  margin-top: 1rem;
  border: none;
  border-radius: 0.2rem;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0.185);
}
.load-more {
  font-family: 'Montserrat', sans-serif;
  background: linear-gradient(to right,#a8dbd8,#cfc9f3, #f7c4d3) repeat-y;
  border-radius: 30px;
  border: 0px solid #c6c6c6;
  color: rgb(50, 8, 90);
  padding: 0.5rem 1.5rem;
  min-width: 60%;
  height:3.5rem;
  font-size:1.3rem;
  margin-top: 3rem;
  cursor: pointer;
  box-shadow: 5px 5px 15px 4px #c2bfbf;
  transition: 0.5s;
}

.load-more:hover{
  box-shadow: 0px 0px 13px #cac7c7;
  transition: 0.5s;
}